import { useEffect, useState } from 'react';
import Heading from '../components/addBranch/Heading';
import AddBranchForm from '../forms/addBranch/AddBranchForm';
import { LoadingSpinner } from '@/components/Loading/LoadingSpinner';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { useParams } from 'react-router-dom';
import { Branch } from '@/types/branches.types';

const EditBranch = () => {
    const params = useParams();
    const { id } = params;
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [branch, setBranch] = useState<Branch | null>(null);

    useEffect(() => {
        const fetchBranch = async () => {
            try {
                const response = await apiGet({
                    endpoint: endpoints.branch.show + id,
                });
                setBranch(response.data.data);
            } catch (error) {
                setError('Failed to load branch details');
            } finally {
                setLoading(false);
            }
        };
        if (id) {
            fetchBranch();
        } else {
            setError('Invalid branch ID');
            setLoading(false);
        }
    }, [id]);

    if (loading) {
        return <LoadingSpinner />;
    }

    if (error) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl text-red-500">{error}</h2>
            </div>
        );
    }

    if (!branch) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl">Branch not found</h2>
            </div>
        );
    }
    return (
        <div className="mt-6 w-full">
            <Heading />
            <AddBranchForm branch={branch} />
        </div>
    );
};

export default EditBranch;
